import React, { useEffect } from "react";
import { Route, useHistory } from "react-router-dom";
import { Main } from "./Main";
import Lecture from "./Lecture";
import { AdminCourse } from "./AdminCourse";
import { AdminUsers } from "./AdminUsers";
import { useSelector } from "react-redux";

export const Admin = ({ match }) => {
    const role = useSelector(({ userStatus }) => userStatus.userInfo && userStatus.userInfo.role);
    const history = useHistory();
    console.log(history);
    useEffect(() => {
        if (history.location.pathname.startsWith("/admin")) role !== "admin" && history.push("/");
        return () => {};
    }, [role, history]);
    return (
        <>
            <Route path={`${match.path}/users`} component={AdminUsers} />
            <Route path={`${match.path}/lecturers`} component={Lecture} />
            <Route path={`${match.path}/courses`} component={AdminCourse} />
            <Route exact path={`${match.path}`} component={Main} />
        </>
    );
};
export default Admin;
