import '../../scssFile/main.css'
import React, { useEffect } from 'react'
import { AdminChart } from '../../Component/AdminChart';
import $ from 'jquery'
export const Main = () => {
    useEffect(() => {
        $('.count').each(function () {
            $(this).prop('Counter',0).animate({
               Counter: $(this).text()
           }, {
               duration: 1000,
               easing: 'swing',
               step: function (now) {
                   $(this).text(Math.ceil(now));
               }
           });
       });
        return () => {
            
        }
    }, [])
    useEffect(() => {
        $(document).change(function() {
            if($("#chart-selection option:selected").val()==="newstudent") {
                $("#line-chart-totalEarning").addClass("disnone");
                $("#line-chart-newStudent").removeClass("disnone");
            } else {
                $("#line-chart-totalEarning").removeClass("disnone");
                $("#line-chart-newStudent").addClass("disnone");
            }
        })
        return () => {
            
        }
    }, [])
    return (
        <div class="content-admin-page ml-75">
            <div class="container-fluid">
                <div class="row">
                    <nav class="navbar navbar-expand-lg navbar-light bg-light"></nav>
                </div>
                <div class="row">
                    <div class="col-md-5 align-self-center"></div>
                    <div class="col-md-7 d-flex justify-content-end align-self-center d-none d-md-flex">
                        <div class="dropdown">
                            <button
                                class="btn btn-info dropdown-toggle"
                                type="button"
                                id="dropdownMenuButton"
                                data-toggle="dropdown"
                                aria-haspopup="true"
                                aria-expanded="false"
                            >
                                Tháng 8
                            </button>
                            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                <a class="dropdown-item" href="#">
                                    Tháng 1
                                </a>
                                <a class="dropdown-item" href="#">
                                    Tháng 2
                                </a>
                                <a class="dropdown-item" href="#">
                                    Tháng 3
                                </a>
                                <a class="dropdown-item" href="#">
                                    Tháng 4
                                </a>
                                <a class="dropdown-item" href="#">
                                    Tháng 5
                                </a>
                                <a class="dropdown-item" href="#">
                                    Tháng 6
                                </a>
                                <a class="dropdown-item" href="#">
                                    Tháng 7
                                </a>
                                <a class="dropdown-item" href="#">
                                    Tháng 8
                                </a>
                                <a class="dropdown-item" href="#">
                                    Tháng 9
                                </a>
                                <a class="dropdown-item" href="#">
                                    Tháng 10
                                </a>
                                <a class="dropdown-item" href="#">
                                    Tháng 11
                                </a>
                                <a class="dropdown-item" href="#">
                                    Tháng 12
                                </a>
                            </div>
                        </div>
                        <div class="dropdown ml-15">
                            <button
                                class="btn btn-success dropdown-toggle"
                                type="button"
                                id="dropdownMenuButton"
                                data-toggle="dropdown"
                                aria-haspopup="true"
                                aria-expanded="false"
                            >
                                2020
                            </button>
                            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                <a class="dropdown-item" href="#">
                                    2019
                                </a>
                                <a class="dropdown-item" href="#">
                                    2018
                                </a>
                                <a class="dropdown-item" href="#">
                                    2017
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-3 col-md-6">
                        <div class="card">
                            <div class="card-body">
                                <h4 class="card-title">Học viên</h4>
                                <div class="text-right">
                                    <h2 class="font-weight-light mb-0">
                                        <i class="fas fa-user-graduate"></i>
                                        <span class="count">350000</span>
                                    </h2>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-lg-3 col-md-6">
                        <div class="card">
                            <div class="card-body">
                                <h4 class="card-title">Khóa học</h4>
                                <div class="text-right">
                                    <h2 class="font-weight-light mb-0">
                                        <i class="fas fa-book"></i>
                                        <span class="count">5000</span>
                                    </h2>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-lg-3 col-md-6">
                        <div class="card">
                            <div class="card-body">
                                <h4 class="card-title">Lượt truy cập</h4>
                                <div class="text-right">
                                    <h2 class="font-weight-light mb-0">
                                        <i class="fas fa-user-graduate"></i>
                                        <span class="count">150000</span>
                                    </h2>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-lg-3 col-md-6">
                        <div class="card">
                            <div class="card-body">
                                <h4 class="card-title">Doanh thu (VND)</h4>
                                <div class="text-right">
                                    <h2 class="font-weight-light mb-0">
                                        <i class="fas fa-dollar-sign"></i>
                                        300.000
                                    </h2>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <AdminChart/>
                    </div>
                </div>
            </div>
        </div>
    );
    
}


