import React, { useEffect } from "react";
import { Chart } from "chart.js";
import $ from "jquery";

export const AdminChart = () => {
    useEffect(() => {
        $(document).ready(function () {
            const ctx = $("#line-chart-totalEarning");
            new Chart(ctx, {
                type: "line",
                data: {
                    labels: ["Tháng 1", "Tháng 2", "Tháng 3", "Tháng 4", "Tháng 5", "Tháng 6", "Tháng 7", "Tháng 8"],
                    datasets: [
                        {
                            label: "Lợi nhuận",
                            data: [10, 20, 17, 64, 45, 70, 77, 15],
                            backgroundColor: "green",
                            borderColor: "green",
                            fill: false,
                            lineTension: 0,
                            pointRadius: 5,
                        },
                    ],
                },
                options: {
                    scales: {
                        yAxes: [
                            {
                                ticks: {
                                    beginAtZero: true,
                                },
                            },
                        ],
                    },
                },
            });
        });
        $(document).ready(function () {
            var ctx = $("#line-chart-newStudent");
            new Chart(ctx, {
                type: "line",
                data: {
                    labels: ["Tháng 1", "Tháng 2", "Tháng 3", "Tháng 4", "Tháng 5", "Tháng 6", "Tháng 7", "Tháng 8"],
                    datasets: [
                        {
                            label: "Học viên mới",
                            data: [1000, 2000, 1700, 1000, 4500, 12000, 10594, 9244],
                            backgroundColor: "green",
                            borderColor: "green",
                            fill: false,
                            lineTension: 0,
                            pointRadius: 5,
                        },
                    ],
                },
                options: {
                    scales: {
                        yAxes: [
                            {
                                ticks: {
                                    beginAtZero: true,
                                },
                            },
                        ],
                    },
                },
            });
        });
        return () => {};
    }, []);
    return (
        <div class="detail-chart">
            <div class="title-selected">
                <h4>Biểu đồ chi tiết</h4>
                <select id="chart-selection" class="ml-15">
                    <option value="totalEarning">Lợi nhuận</option>
                    <option value="newstudent">Học viên</option>
                </select>
            </div>
            <canvas id="line-chart-totalEarning" class=""></canvas>
            <canvas id="line-chart-newStudent" class="disnone"></canvas>
        </div>
    );
};
